












	import { Component, Vue } from "vue-property-decorator";

    declare global {
		interface Window { videojs: any,sermon: any }	
	}

	var touchEvent = 'onclick' in window ? 'click' : 'touchstart';

	@Component
	export default class LiveStream extends Vue {
		remoteVideo: HTMLVideoElement;
		isPlaying: boolean = false;
		player:any = null;
		paused: boolean = false;
		newStyle: any = [];
		isMuted: boolean = false;
		currentTime = '';
		intervalId = null;
		remainingTime = '';

		public mounted() {
			this.player = window.videojs('sermon-video', {
				controls: true,
				responsive:true, 
				controlBar: {
					children:[
						"playToggle",
						"volumePanel",		
						"progressControl",
						"currentTimeDisplay",
						"timeDivider",
						"durationDisplay",
						]
				}
			});

			this.player.ready(() => {
				this.player.controlBar.playToggle.on('click', (e) => {
					this.isPlaying? this._pause() : this._play();
				});
			});

			this.player.on('timeupdate', (e) => 
				this.remainingTime = this.formatTime(this.player.remainingTime())
			);

			this.player.on('ended', () => this.isPlaying = false);
			window.onbeforeunload = () => {
				if(this.intervalId != null) clearInterval(this.intervalId);
			}
			
			window.addEventListener('playsermon', (e:any) => {
				this.player.src({ type: 'video/mp4', src: window.sermon });
				this._play();
			});
		}

		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

		formatTime(seconds, guide = seconds) {
			 seconds = seconds < 0 ? 0 : seconds;
			let s = Math.floor(seconds % 60);
			let m = Math.floor(seconds / 60 % 60);
			let h = Math.floor(seconds / 3600);
			const gm = Math.floor(guide / 60 % 60);
			const gh = Math.floor(guide / 3600);
			
			// handle invalid times
			if (isNaN(seconds) || seconds === Infinity) {
				h = m = s = 0 ; 
			}

			var hours = (h > 0 || gh > 0) ? h + ':' : '';
			var minute  = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':';
			var second = (s < 10) ? '0' + s : s;

			return hours + minute + second;
		}

		_play() {
			this.player.play();
			this.isPlaying = true;
		}

		_pause() {
			this.player.pause();
            this.isPlaying = false;
		}

		_mute() {
			this.player.muted(true);
			this.isMuted = true;
		}

		_unmute() {
			this.player.muted(false); 
			this.isMuted = false;
		}
}
